(function() {
  'use strict';

  angular
    .module('arAdmin')
    .factory('Auth', Auth);

  /** @ngInject */
  function Auth ($firebaseAuth, FIREBASE_URL) {
    var ref = new Firebase(FIREBASE_URL);
    return $firebaseAuth(ref);
  }

})();

(function() {
  'use strict';

  angular
    .module('arAdmin')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController ($log, $state, Auth) {

    var vm = this;

    vm.loginWithPassword = function () {
      Auth.$authWithPassword(vm.user)
      .then(function (authData) {
        _slaask.identify(authData.password.email, {
           user_id: authData.uid,
           email: authData.password.email,
       });
        $state.go('li.matchups.index');
      })
      .catch( function (error) {
        $log.error('Error: ' + error);
      });
    }


  }

})();

(function() {
  'use strict';

  angular
    .module('arAdmin')
    .controller('AccountController', AccountController);

  /** @ngInject */
  function AccountController ($state, $mdSidenav, Auth, CurrentAuth) {

    var vm = this;
    vm.me = CurrentAuth;
    vm.data = $state.current.data;
    vm.go = function (to, params, options) {
      $state.go(to, params, options);
    }

    /*
     * Functions
     */

    vm.logout = function () {
      // log the user out
      Auth.$unauth();

      // then do something
      $state.go('login');
    }

    vm.toggleSidenav = function () {
      $mdSidenav('settings').toggle();
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('arAdmin')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $state, $mdToast) {

    var vm = new Object;
    $log.debug('runBlock end');

    // TODO: Why isn't this working?
    // vm.on = $rootScope.$on();
    // vm.on("$stateChangeError", function (event, toState, toParams, fromState, fromParams, error) {
    //   // throw the error
    //   $log.error(error);
    //
    //   // We can catch the error thrown when the $requireAuth promise is rejected
    //   // and redirect the user back to the home page
    //   if (error === "AUTH_REQUIRED") {
    //     $state.go("login");
    //   }
    // });

    $rootScope.$on('$stateChangeError', function (event, toState, toParams, fromState, fromParams, error) {
      // throw the error
      $log.error(error);

      // We can catch the error thrown when the $requireAuth promise is rejected
      // and redirect the user back to the home page
      if (error === "AUTH_REQUIRED") {
        $state.go("login");
      }
    });

    // TODO: Why isn't this working?
    // vm.on("$stateChangeStart", function (event, toState, toParams, fromState, fromParams, error) {
    //   $mdToast.cancel();
    // });

    $rootScope.$on("$stateChangeStart", function (event, toState, toParams, fromState, fromParams, error) {
      $mdToast.cancel()
        .then( function (response) {
          $rootScope.toastDeployed = false;
        });
    })

  }

})();

(function() {
  'use strict';

  angular
    .module('arAdmin')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('login', {
        url: '/login',
        templateUrl: 'app/account/tmpl.login.html',
        controller: 'LoginController',
        controllerAs: 'LoginCtrl'
      })
      .state('li', {
        url: '/app',
        templateUrl: 'app/account/tmpl.li.html',
        controller: 'AccountController',
        controllerAs: 'AccountCtrl',
        abstract: true,
        resolve: {
          // controller will not be loaded until $waitForAuth resolves
          // Auth refers to our $firebaseAuth wrapper in the example above
          "CurrentAuth": ["Auth", function(Auth) {
            // $waitForAuth returns a promise so the resolve waits for it to complete
            return Auth.$requireAuth();
          }]
        }
      })
      .state('li.matchups', {
        url: '/matchups',
        abstratc: true,
        template: '<ui-view flex layout="column" />',
        controller: 'MatchupsController',
        controllerAs: 'MatchupsCtrl'
      })
      .state('li.matchups.index', {
        url: '/index',
        templateUrl: 'app/matchups/tmpl.index.html',
        controller: 'MatchupsIndexController',
        controllerAs: 'MatchupsIndexCtrl',
        data: {
          title: 'Matchups Index'
        }
      })
      .state('li.matchups.detail', {
        url: '/id/:id',
        templateUrl: 'app/matchups/tmpl.detail.html',
        data: {
          title: 'Matchup Detail'
        }
      })
      .state('li.matchups.edit', {
        url: '/id/:id/edit',
        templateUrl: 'app/matchups/tmpl.edit.html',
        controller: 'MatchupsEditController',
        controllerAs: 'MatchupsEditCtrl',
        data: {
          title: 'Edit Matchup'
        }
      })
      .state('li.matchups.create', {
        url: '/create',
        templateUrl: 'app/matchups/tmpl.create.html',
        data: {
          title: 'Create Matchup'
        }
      });

    $urlRouterProvider.otherwise('/app/matchups/index');

  }

})();

